@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.min.css);
header {
  text-align: center;
  margin: 3rem 0 2rem;
  -webkit-filter: drop-shadow(0 0 1rem rgb(0,0,0));
          filter: drop-shadow(0 0 1rem rgb(0,0,0))
}

header .logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
}

header img {
  display: inline-block;
  height: 6vh;
}

header h1 {
  font-size: 2rem;
  margin: 0;
}

header h1::before {
  content: "";
  border-left: 1px solid var(--fgColor);
  margin: 0 2rem;
}

header p {
  margin: 0;
}

@media screen and (min-width: 800px) {
  header {
    margin-top: 4rem;
  }
}

.searchform {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: var(--defaultColor);
  margin-bottom: 2rem;
  position: relative;
}

.searchform input,
.searchform select,
.searchform button {
  background: none;
  border: none;
  outline: none;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  padding: 1rem;
  border-radius: 0;
  transition: all .2s ease;
}

.searchform input {
  flex: 1 1;
}

.searchform select {
  border-top: 1px solid var(--defaultColor);
  -webkit-appearance: none;
}

.searchform span {
  position: relative;
  width: 0;
  height: 0;
  top: -2.2rem;
  left: 82vw;
  pointer-events: none;
  opacity: 0.7;
  font-size: 0.7rem;
}

.searchform input:focus,
.searchform select:focus {
  background: var(--defaultColor);
}

.searchform option {
  background: #000000;
}

.searchform button {
  padding: 1rem 2rem;
  cursor: pointer;
  background: var(--focusColor);
  text-transform: uppercase;
  transition: all .2s ease;
}

.searchform button:hover,
.searchform button:focus {
  background: var(--accentColor);
}

.searchform button:disabled {
  opacity: 0.5;
  background: var(--defaultColor);
  cursor: not-allowed;
}

@media screen and (min-width: 800px) {

  .searchform {
    flex-direction: row;
    margin-bottom: 3rem;
  }

  .searchform select {
    border: none;
    margin: 0;
  }

  .searchform span {
    left: -1.5rem;
    top: 1.1rem;
  }

}



.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  height: 0;
  transition: all .2s ease;
}

.loader.fadeIn {
  height: 1rem;
  margin: 1rem 0;
}

.loader span {
  height: 1rem;
  width: .4rem;
  margin: 0 .15rem;
  background: var(--focusColor);
  -webkit-animation: load 1s ease infinite;
          animation: load 1s ease infinite;
}

.loader span:nth-of-type(2) {
  -webkit-animation-delay: .1s;
          animation-delay: .1s;
}

.loader span:nth-of-type(3) {
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
}

@-webkit-keyframes load {
  0%, 60%, 100% { background: var(--focusColor); }
  80% { background: var(--accentColor); }
}

@keyframes load {
  0%, 60%, 100% { background: var(--focusColor); }
  80% { background: var(--accentColor); }
}

.results {
  margin: 0 0 3rem;
  padding: 0;
  list-style: none;
}

.results p {
  text-align: center;
  margin-bottom: 2rem;
}

.results .error {
  color: #F27166;
}

.results .buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  border: none;
  background: none;
}

.results .btn {
  padding: 1rem 2rem;
  border: none;
  outline: none;
  cursor: pointer;
  background: var(--defaultColor);
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  text-transform: uppercase;
  transition: all .2s ease;
}

.results .btn:hover,
.results .btn:focus {
  background: var(--focusColor);
}

.results .btn:disabled {
  opacity: 0.5;
  background: var(--defaultColor);
  cursor: not-allowed;
}

.results button:not(.btn)  {
  background: none;
  border: none;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
  padding: 0;
  outline: none;
  text-align: left;
  transition: all .2s ease;
  max-width: 90%;
}

.results li[crawl] button {
  cursor: pointer;
}

.results li[crawl] button:hover {
  color: var(--accentColor);
}

.results li {
  border: 1px solid var(--defaultColor);
  background: var(--resultColor);
  padding: 1.2rem;
  margin-bottom: 2rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: all .2s ease;
}

.results li:not(.buttons):hover,
.results li:not(.buttons):focus-within {
  background: var(--hoverColor);
  border-color: var(--fgColor);
}

.results li h2 {
  margin: 0;
}

.results li .stats {
  display: flex;
  flex-wrap: wrap;
  max-height: 0;
  opacity: 0;
  transition: all .4s linear;
  overflow: hidden;
}

.results li:hover .stats,
.results li:focus-within .stats {
  margin-top: .5rem;
  max-height: 100vh;
  opacity: 1;
}

.results li .stats .stat {
  width: 50%;
  text-transform: uppercase;
  color: var(--focusColor);
  margin-bottom: 1rem;
}

.results li .stats .stat b {
  color: var(--statColor);
  display: block;
  width: 90%;
}
.crawl-space {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-image: url(/star-wars-search/static/media/starfield.34ea7c1c.jpg);
  background-color: #222222;
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 400px;
  overflow: hidden;
  z-index: 5;
}

.crawl-space button {
  position: absolute;
  z-index: 10;
  top: 2rem;
  right: 2rem;
  background: #FFFFFF;
  padding: 1rem;
  border: none;
  outline: none;
  font-family: inherit;
  font-size: inherit;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
  transition: all .2s ease;
}

.crawl-space button:hover,
.crawl-space button:focus {
  background: #DDDDDD;
}

.crawl-space::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: linear-gradient(0deg, transparent, #000000 75%);
  z-index: 5;
}

.crawl-space .crawl {
  color: #FFD700;
  position: relative;
  top: 0;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  font-size: 3rem;
  font-weight: bold;
  transform-origin: 50% 100%;
  -webkit-animation: crawl 60s linear forwards;
          animation: crawl 60s linear forwards;
}

.crawl-space .crawl h2 {
  font-size: 1em;
}

@-webkit-keyframes crawl {
  0% {
    top: 120rem;
    transform: rotateX(40deg) translateZ(0);
  }
  100% {
    top: -200rem;
    transform: rotateX(45deg) translateZ(-120rem);
  }
}

@keyframes crawl {
  0% {
    top: 120rem;
    transform: rotateX(40deg) translateZ(0);
  }
  100% {
    top: -200rem;
    transform: rotateX(45deg) translateZ(-120rem);
  }
}

@media screen and (min-width: 800px) {
  .crawl-space .crawl {
    font-size: 6rem;
  }
  
  @-webkit-keyframes crawl {
    0% {
      top: 270rem;
      transform: rotateX(40deg) translateZ(0);
    }
    100% {
      top: -400rem;
      transform: rotateX(45deg) translateZ(-120rem);
    }
  }
  
  @keyframes crawl {
    0% {
      top: 270rem;
      transform: rotateX(40deg) translateZ(0);
    }
    100% {
      top: -400rem;
      transform: rotateX(45deg) translateZ(-120rem);
    }
  }
}
footer {
  padding: 2rem 0;
  text-align: center;
  font-size: 0.8rem;
}

footer a {
  color: inherit;
}

footer .links {
  margin-bottom: 2rem;
  z-index: 0 !important;
}

footer .links #st-1 {
  font-family: inherit !important;
}

footer .links .st-total {
  color: var(--fgColor) !important;
}
.App {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.App main {
  flex: 1 1;
}
* {
  box-sizing: border-box;
  outline-color: var(--accentColor);
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  --bgColor: rgb(30, 36, 45);
  --fgColor: rgb(255,255,255);
  --defaultColor: rgba(255,255,255,0.2);
  --focusColor: rgba(255,255,255,0.3);
  --statColor: rgba(255,255,255,0.7);
  --accentColor: rgb(230, 186, 43);
  --resultColor: rgba(0,0,0,0.75);
  --hoverColor: rgb(0,0,0);
}

body {
  font-family: 'Roboto Mono', monospace;
  background-image: url(/star-wars-search/static/media/starfield.34ea7c1c.jpg);
  background-color: #222222;
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-color: var(--bgColor);
  color: var(--fgColor);
}

#root {
  height: 100%;
}

@-webkit-keyframes movearound {
  0%, 100% {
    background-position: 0 0;
  }
  25% {
    background-position: 0 50%;
  }
  50% {
    background-position: 50% 50%;
  }
  75% {
    background-position: 50% 0;
  }
}

@keyframes movearound {
  0%, 100% {
    background-position: 0 0;
  }
  25% {
    background-position: 0 50%;
  }
  50% {
    background-position: 50% 50%;
  }
  75% {
    background-position: 50% 0;
  }
}

.animated {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}

::selection {
  background: var(--accentColor);
}

*::-webkit-scrollbar {
  width: .5rem;
  z-index: 0;
}
 
*::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
*::-webkit-scrollbar-thumb {
  background-color: var(--accentColor);
  outline: 1px solid var(--accentColor);
}

header, main {
  width: 90%;
  max-width: 800px;
}
